/*---------------------------basic-setting-grid-----------------*/

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  display: flex;
  padding-right: 10px!important;
  padding-left: 10px!important;
  max-width: 1177px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  justify-content: center;
}

html {
  font-size: 10px;
}

/*-------------------------------fonts-------------------------*/

.lato-regular {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}

.lato-light {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
}

/*-------------------------------header-----------------------*/

.header-line {
  box-sizing: border-box;
  height: 3px;
  background: url(../img/line.jpg);
  background-size: cover;
}

header {
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 103px;
  background-color: #fff;
}

header .wrapper {
  box-sizing: border-box;
  height: 103px;
}

header .header-logo {
  flex-grow: 3;
  align-self: center;
}

header .nav-menu_mobile {
  padding: 5px 10px;
}

.sticky {
  margin-top: -3px;
  transition: all 0.4s ease;
}

/*--------------------------------nav--------------------------*/

.header-navigation {
  display: flex;
  flex-direction: column;
  margin-right: 10px;
  flex-grow: .3;
  justify-content: center;
}

.header-navigation_bar {
  display: flex;
  background-color: #fff;
  justify-content: center;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
}

.header-navigation_bar .navside-bar {
  position: relative;
  margin-right: 40px;
}

.header-navigation_bar .navside-bar:last-child {
  margin-right: 0;
}

.header-navigation_bar .navside-bar .nav-right {
  left: -231px;
}

.header-navigation_bar li {
  font-size: 18px;
  position: relative;
}

.header-navigation_bar li:hover > a {
  color: #41bedd;
}

.header-navigation_bar li:hover > ul {
  visibility: visible;
  opacity: 1;
}

.header-navigation_bar a {
  text-align: left;
  text-decoration: none;
  color: #34383d;
}

.header-navigation_bar ul {
  position: absolute;
  z-index: 1;
  top: 30px;
  left: -34px;
  visibility: hidden;
  transition: all 0.2s ease-in-out;
  opacity: 0;
  border-radius: 3px;
  background: #fff;
  background: linear-gradient(#fff, #fff);
  box-shadow: 0 -1px 0 rgba(255, 255, 255, 0.3);
}

.header-navigation_bar ul ul {
  top: 0;
  left: 170px;
  margin: 0 0 0 20px;
  box-shadow: -1px 0 0 rgba(255, 255, 255, 0.3);
}

.header-navigation_bar ul ul li {
  font-size: 16px;
}

.header-navigation_bar ul ul li:first-child a:after {
  top: 50%;
  left: -6px;
  margin-top: -6px;
  border-top: 6px solid transparent;
  border-right: 6px solid #222;
  border-bottom: 6px solid transparent;
  border-left: 0;
}

.header-navigation_bar ul ul li:first-child a:hover:after {
  border-right-color: #0299d3;
  border-bottom-color: transparent;
}

.header-navigation_bar ul li {
  font-size: 16px;
  display: block;
  float: none;
  border: 0;
  box-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc;
  _line-height: 0;
}

.header-navigation_bar ul a {
  display: block;
  float: none;
  padding: 10px;
  white-space: nowrap;
  text-transform: none;
  _height: 10px;
}

:before :after {
  display: table;
  content: '';
}

/*-----------------------------------nav-mobile----------------------------*/

/*------------------------------------search----------------------------*/

.header-search {
  display: flex;
  box-sizing: border-box;
  align-self: center;
}

input {
  outline: none;
}

input[type=search] {
  font-family: inherit;
  box-sizing: content-box;
  width: 55px;
  padding: 9px 10px 9px 32px;
  transition: all .5s;
  border: solid 1px #ccc;
  border-radius: 10em;
  background: #fff url(../img/search.svg) no-repeat 9px center;
  background-size: 19px;
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
  display: none;
}

input[type=search]:focus {
  width: 130px;
  border-color: #41bedd;
  background-color: #fff;
  -webkit-box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
  -moz-box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
  box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
}

#search input[type=search] {
  width: 15px;
  padding-left: 10px;
  cursor: pointer;
  color: transparent;
}

#search input[type=search]:hover {
  background-color: #fff;
}

#search input[type=search]:focus {
  width: 130px;
  padding-left: 32px;
  cursor: auto;
  color: #000;
  background-color: #fff;
}

#search input:-moz-placeholder {
  color: transparent;
}

#search input::-webkit-input-placeholder {
  color: transparent;
}

/*---------------------------------main-------------------------------*/

main {
  margin-top: 103px;
  box-sizing: border-box;
  /*----------------------------------masonry-----------------------------*/
  /* ---- grid ---- */
  /* clearfix */
  /* ---- grid-item ---- */
}

main .main-grid_navigation {
  margin-top: 67px;
  margin-bottom: 30px;
  text-align: center;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}

main .main-grid_navigation .navigation-button {
  font-size: 14px;
  margin: 0 -2px;
  padding: 7px 29px;
  border: solid 1px #bdc3c7;
  border-radius: 4px;
  outline: none;
  background-color: rgba(57, 59, 65, 0);
}

main .main-grid_navigation .navigation-button:hover {
  color: #fff;
  background-color: #0299d3;
}

main .main-bg {
  display: flex;
  flex-direction: column;
  height: 300px;
  background: url(../img/main-taitel.jpg);
  background-size: cover;
  justify-content: center;
}

main .main-bg .main-taitel {
  font-size: 60px;
  margin: 0 auto;
  margin-bottom: 13px;
  margin-left: 15px;
  text-align: center;
  color: #fff;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
}

main .main-bg .main-taitel .taietl-arrow {
  font-size: 16px;
  display: flex;
  margin-top: 4px;
  margin-right: 15px;
  justify-content: center;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}

main .main-bg .main-taitel .taietl-arrow i {
  margin-right: 10px;
  margin-left: 10px;
}

main * {
  box-sizing: border-box;
}

main .main-grid {
  box-sizing: border-box;
}

main .main-grid:after {
  display: block;
  clear: both;
  content: '';
}

main .main-grid_item {
  margin-bottom: 32.5px;
  background: #fff;
}

main .main-grid_item--height1 {
  width: 370px;
  height: 370px;
}

main .main-grid_item--height2 {
  width: 370px;
  height: 270px;
}

main .main-grid_item--height3 {
  width: 370px;
  height: 170px;
}

main .main-grid_item--height4 {
  width: 370px;
  height: 210px;
}

main .main-grid_item--height5 {
  width: 370px;
  height: 510px;
}

main .main-grid_button {
  margin-top: 17px;
  margin-bottom: 71px;
  text-align: center;
}

main .main-grid_button button {
  width: 125px;
  height: 35px;
  cursor: pointer;
  border: solid 1px #bdc3c7;
  border-radius: 4px;
  outline: none;
  background-color: rgba(57, 59, 65, 0);
}

main .main-grid_button button:hover {
  color: #fff;
  background-color: #0299d3;
}

/*---------------------------------------------footer----------------------------------------*/

footer {
  background-color: #ecf0f1;
}

footer .footer-info {
  height: auto;
  box-sizing: border-box;
  margin-right: 0;
  margin-left: 0;
  justify-content: center;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

footer .footer-info .info-about_us {
  font-size: 20px;
  margin-top: 30px;
  color: #34383d;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(31.666666666666668% - 10px);
  align-self: center;
}

footer .footer-info .info-about_us p {
  font-size: 16px;
  line-height: 24px;
  margin-top: 18px;
  color: #606366;
}

footer .footer-info .info-contact_us {
  font-size: 20px;
  margin-top: 30px;
  margin-right: 18px !important;
  margin-left: 18px !important;
  color: #34383d;
  align-self: center;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(19.166666666666668% - 10px);
}

footer .footer-info .info-contact_us img {
  position: relative;
  top: 5px;
  margin-right: 12px;
}

footer .footer-info .info-contact_us p {
  font-size: 1.6rem;
  line-height: 32px;
  display: inline-block;
  width: 110%;
  color: #606366;
}

footer .footer-info .info-contact_us .info-contact_us-phone {
  margin-top: 14.2px;
}

footer .footer-info .info-locate {
  margin-right: 32px !important;
  margin-left: 28px !important;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(16.666666666666668% - 10px);
}

footer .footer-info .info-locate p {
  font-size: 1.6rem;
  line-height: 24px;
  margin-top: 62px;
  margin-left: 34px;
  color: #606366;
}

footer .footer-info .info-locate img {
  position: relative;
  top: 88px;
}

footer .footer-info .info-locate .google-maps {
  width: 191px;
  height: 35px;
  margin-top: 15px;
  cursor: pointer;
  border: solid 1px #bdc3c7;
  border-radius: 4px;
  outline: none;
  background-color: rgba(57, 59, 65, 0);
}

footer .footer-info .info-locate .google-maps:hover {
  color: #fff;
  background-color: #0299d3;
}

footer .footer-info .info-connected {
  font-size: 20px;
  flex-direction: column;
  margin-top: 45px;
  margin-right: 0 !important;
  margin-left: 2%!important;
  color: #34383d;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(23.333333333333332% - 10px);
}

footer .footer-info .info-connected .info-connected_taitel {
  margin-bottom: 32px;
}

footer .footer-info .info-connected .info-connected_social {
  display: flex;
  width: auto !important;
  margin-top: 20px;
