}

footer .footer-info .info-connected .info-connected_social li a #youtb:hover {
  background-color: #ce2f28;
}

footer .footer-info .info-connected .info-connected_social li a #vimeo:hover {
  background-color: #42bdff;
}

footer .footer-info .info-connected .info-connected_social li a #instagram:hover {
