}

.button-up {
  position: absolute;
  align-self: center;
  margin-top: -58px;
}

.button-up img {
  width: 40px;
}

/*--------------------------------media-----------------------------*/

/*--------------------------------ie10-ie11-----------------*/

@media only screen and (min-width: 992px) {
  .nav-menu_mobile {
    display: none;
  }

  .mobile {
    display: none;
  }
}

@media only screen and (max-width: 1168px) {
  .info-connected_social li:nth-child(10),
  .info-connected_social li:nth-child(5) {
    margin-right: 14px !important;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  header .header-logo {
    display: block;
    margin-right: 10px;
    margin-left: 82px;
    margin-left: 40px;
    flex-grow: .7;
  }

  .header-navigation {
    position: relative;
    top: 38px;
    display: block;
    width: 100px;
    margin-right: 10px;
    flex-grow: 0;
  }

  .header-navigation_bar {
    display: none;
    margin: 0 -23px;
  }

  .header-navigation_bar ul {
    top: 11px;
    left: -193px;
  }

  .header-navigation_bar .navside-bar .nav-right {
    left: 173px;
  }

  .lg {
    display: none;
  }

  .header-search {
    width: 40px;
    flex-grow: 1;
  }

  main .main-bg .main-taitel {
    font-size: 40px;
  }

  footer .footer-info .info-about_us {
    width: calc(50% - 10px);
  }

  footer .footer-info .info-about_us p {
    font-size: 14px;
  }

  footer .footer-info .info-contact_us {
    width: calc(25% - 10px);
  }

  footer .footer-info .info-contact_us p {
    font-size: 1.3rem;
    width: 130%;
  }

  footer .footer-info .info-locate {
    width: calc(33.333333333333336% - 10px);
  }

  footer .footer-info .info-connected {
    width: calc(33.333333333333336% - 10px);
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  header .header-logo {
    margin-left: 40px;
  }

  footer .footer-info .info-about_us {
    width: calc(83.33333333333334% - 10px);
  }

  footer .footer-info .info-contact_us {
    width: calc(33.333333333333336% - 10px);
  }

  footer .footer-info .info-locate {
    width: calc(33.333333333333336% - 10px);
  }

  footer .footer-info .info-connected {
    width: calc(46.666666666666664% - 10px);
  }

  footer .footer-info .info-connected .info-connected_social {
    width: 105% !important;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  header .header-logo {
    margin-left: 91px;
  }

  .header-navigation {
    flex-grow: 3;
  }

  .header-search {
    position: relative;
    left: -55px;
    flex-grow: 14;
  }

  footer .footer-info .info-contact_us {
    width: calc(41.66666666666667% - 10px);
  }

  footer .footer-info .info-contact_us p {
    width: 130%;
  }

  footer .footer-info .info-locate {
    width: calc(41.66666666666667% - 10px);
  }

  footer .footer-info .info-connected {
    width: calc(83.33333333333334% - 10px);
  }

  footer .footer-info .info-connected .info-connected_social {
    width: 70% !important;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media only screen and (max-width: 1149px) and (min-width: 992px) {
  html {
    font-size: 8.2px;
  }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .button-up {
    position: relative;
    top: -41px;
  }

  .wrapper {
    align-self: center;
  }
}